/* General Reset */
body, html {
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  background: #121212;
  color: #ffffff;
  overflow: hidden;
}

/* App Container */
.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  position: relative;
}

/* Title */
.title {
  font-size: 3rem;
  font-weight: bold;
  color: #1db954;
  margin-bottom: 20px;
  text-shadow: 0px 0px 20px #1db954;
}

/* Content Container */
.content-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 90%;
  overflow: hidden;
  position: relative;
}

/* Scroll View */
.scroll-view {
  display: flex;
  transition: transform 0.6s ease-in-out;
  transform: translateX(calc(-100% * var(--current-index, 0)));
}

.section {
  min-width: 100%;
  text-align: center;
  opacity: 0.5;
  transition: opacity 0.6s ease-in-out;
}

.section.active {
  opacity: 1;
}

.section h2 {
  font-size: 2rem;
  color: #1db954;
  text-shadow: 0px 0px 15px #1db954;
  margin-bottom: 15px;
}

.section ul {
  list-style: none;
  padding: 0;
}

.section li {
  font-size: 1.2rem;
  margin: 10px 0;
}

/* Navigation Buttons */
.nav-button {
  background: transparent;
  border: 2px solid #1db954;
  color: #1db954;
  font-size: 1.5rem;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 50%;
  margin: 0 10px;
  transition: background 0.3s, color 0.3s, box-shadow 0.3s;
}

.nav-button:hover {
  background: #1db954;
  color: #121212;
  box-shadow: 0px 0px 15px #1db954;
}

/* Smooth Glow Animation */
@keyframes glow {
  0% {
    box-shadow: 0px 0px 10px #1db954;
  }
  50% {
    box-shadow: 0px 0px 20px #1db954;
  }
  100% {
    box-shadow: 0px 0px 10px #1db954;
  }
}

.nav-button:hover {
  animation: glow 1.5s infinite;
}
